@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*, *::before, *::after{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html,body {
    height:100%;
    font-family: 'Raleway', sans-serif;
}
.main {
    width: 100%;
    height: 100vh;
    transition: background-color 1000ms linear;
    background-image: linear-gradient(to bottom, #ffd300, #ffbe00, #ffa800, #ff9100, #ff7a00);
}

.blue{
transition: background-color 1000ms linear;
    background-image: linear-gradient(to bottom, #43b3fb, #2fabfd, #1ba2fe, #0499ff, #0090ff);
}
.yellow{
transition: background-color 1000ms linear;
    background-image: linear-gradient(to bottom, #ffd300, #ffbe00, #ffa800, #ff9100, #ff7a00);
}
.gray{
transition: background-color 1000ms linear;
background-image: linear-gradient(to bottom, #c4e8ff, #adceea, #98b4d4, #859bbe, #7482a8);
}
.search-form{
    display: flex;
    width: 100%;
    height: 200px;
    justify-content: center;
    align-items: center;
}

.search-form__input{
    font-family: 'Raleway', sans-serif;
    width: 240px;
    height: 50px;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid #fff;
    outline: none;
    font-size: 2rem;
    color: #fff;
    text-align: center;
}

.search-form__input::-webkit-input-placeholder{
    color: rgba(255, 255, 255, 0.473);
}

.search-form__input::-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.473);
}

.search-form__input::placeholder{
    color: rgba(255, 255, 255, 0.473);
}
.weather-result{
   display: flex;
   justify-content: center;
   align-items: center;
   width: 100%;
   height: calc(100% - 200px);
   color: #fff;
}
.weather-result__card{
    font-size: 1.7rem;
   width: 500px;
   display: flex;
   justify-content: center;
   flex-direction: column;
   align-items: center;
   margin-top: -80px;
}

.card__title{
    text-transform: capitalize;
    margin-bottom: .5em;
    text-align: center;
}

.card__temp{
    text-transform: capitalize;
    margin: .5em 0;
}
.card__desc{
    text-transform: capitalize;
}
